<template>
    <el-card :body-style="{padding:0,height:'100%'}" shadow="always" class="box-card align">
        <el-row type="flex" class="row">
            <el-col class="padding left-base" :class="{'left-offset':lor}">
                <el-row type="flex" justify="center">
                    <el-col>
                        {{t2}}
                    </el-col>
                </el-row>
                <el-row type="flex" justify="start">
                    <el-col>
                        <el-input v-model="username" placeholder="用户名" prefix-icon="el-icon-user"></el-input>
                    </el-col>
                </el-row>
                <el-row type="flex" justify="start">
                    <el-col>
                        <el-input v-model="password" placeholder="密码" prefix-icon="el-icon-lock"></el-input>
                    </el-col>
                </el-row>
                <br>
                <el-row type="flex" justify="center">
                    <el-button type="primary" round @click="lorM">{{t2}}</el-button>
                </el-row>
            </el-col>
            <el-col class="background right-base" :class="{'right-offset':lor}" justify="center">
                <el-row type="flex" justify="center">
                    <el-col class="row-r">
                        <i class="el-icon-right cycle" :class="{'cycle-rotate':lor}" @click="changeLOR"></i>
                        <span class="text">{{t1}}</span>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
    </el-card>
    
</template>

<script>
    import Log from './self/Log'
    import axios from 'axios'
    export default {
        data() {
            return {
                username: 'admin',
                password: '123456',
                lor:false,
                t1: '注册',
                t2: '登录'
            }
        },
        methods: {
            lorM() {
                const __t = this;
                let l = this.lor;
                let t = !l ? '/login.do' : '/register.do'
                let m = !l ? '登录' : '注册';
                axios.post(__t.$store.state.s + t, {
                    username:__t.username,
                    password: __t.password
                  }).then(function (response) {
                      
                      Log.put('用户' + __t.username + '尝试' + m)
                    if(response.data.code == 200) {
                        if(l) {
                            __t.changeLOR()
                        } else {
                            __t.$store.state.user.user = response.data.data.user;
                            __t.$store.state.user.isExist = true;
                            __t.$router.push("/home")
                            Log.put('用户' + __t.username + m + '成功')
                        }
                    } else {
                        Log.put('用户' + __t.username + m + '失败')
                        __t.$notify.info({
                                  title: '状态码:' + response.data.code,
                                  message: response.data.msg,
                                  duration: 10000
                                });
                        return ;
                    }
                  });
            },
            changeLOR() {
                // let m = !this.lor ? '登录' : '注册';
                // Log.put('切换为' + m + '界面')
                this.lor = !this.lor;
                let t = this.t1;
                this.t1 = this.t2;
                this.t2 = t;
            }
        }
    }
</script>

<style>
    .left-base {
        position: relative;
        left: 0px;
        transition: all 1s ease 0s;
    }
    .left-offset {
        left: 213px;
        transition: all 1s ease 0s;
        
    }
    .right-base {
        position: relative;
        right: 0px;
        transition: all 1s ease 0s;
    }
    .right-offset {
        right: 237px;
        transition: all 1s ease 0s;
    }
    .text {
        position: relative;
        left: 10px;
        bottom: 9px;
        font-size: 20px;
        color: white;
    }
    .row {
        height: 100%;
    }
    .row-r {
        position: relative;
        white-space: nowrap;
        top: 125px;
        left: 55px;
        vertical-align: middle;
    }
    .cycle {
        border: 2px white solid;
        border-radius: 50%;
        color: white;
        font-size: 50px;
        transform:rotate(180deg);
        transition: all 1s ease 0s;
    }
    .cycle-rotate {
        transform:rotate(0deg);
        transition: all 1s ease 0s;
    }
    
    .align {
        position: absolute;
        z-index: -1;
        top: calc(50vh - 150px);
        left: calc(50vw - 205px);
        width: 450px;
        height: 300px;
    }
    .padding,
    .padding > div{
        padding: 5%;
    }
    
    .background {
        width: 100%;
        height: 100%;
        background-color: #47a0ff;
    }
</style>
