<template>
    <el-card class="pf" style="height: 100%; width: 270px;" :body-style="{margin:'0px',padding: '0px' }">
        <el-image style="width: 270px;height: 270px;" :src="server + '/goods/downloadImg/' + goods.name" :fit="'fit'"></el-image>
        <div class="ttt" style="display: block;padding: 10px;">
            <div><strong>{{goods.name}}</strong></div>
            <div>价格:{{goods.price}}</div>
            <div>{{content}}</div>
            <el-button @click="buy" type="primary" :disabled="disabled" size="medium" class="button">购买</el-button>
        </div>
    </el-card>
</template>
<script>
    import axios from '../../httpConfig/http.js'
    import Log from './Log.vue'

    export default {
        data() {
            return {
                hour: 0,
                minute: 0,
                seconds: 0,
                disabled: false,
                content: '抢购开始时间' + this.goods.buyStart,
            }
        },
        props: {
            server: null,
            goods: null
        },
        methods: {
            buy() {
                let __t = this
                axios.get(__t.server + '/buy?id=' + __t.goods.goodId).then(function(response) {
                    if (response.data.code == 200) {
                        __t.$notify({
                            title: '状态码:' + response.data.code,
                            message: response.data.msg,
                            duration: 10000,
                            type: 'success'
                        });
                        Log.put('购买' + __t.goods.name + '成功')
                    } else {
                        Log.put('购买' + __t.goods.name + '失败')
                        __t.$notify.info({
                            title: '状态码:' + response.data.code,
                            message: response.data.msg,
                            duration: 10000
                        });
                    }
                });
            }
        },
        mounted() {
            let __t = this;
            let start = parseInt(Date.parse(__t.goods.buyStart) / 1000);
            let end = parseInt(Date.parse(__t.goods.buyEnd) / 1000);
            if (isNaN(end)) {
                __t.disabled = false
                __t.content = '未限制购买时间'
                return;
            }
            let cur = parseInt(new Date() / 1000);
            let remain = 0;
            if (start > cur) {
                __t.disabled = true;
            }
            let msg = '开始倒计时：'
            let inter = setInterval(function() {
                if (cur >= end) {
                    __t.content = '活动已结束'
                    __t.disabled = true
                    window.clearInterval(inter)
                    return;
                }
                cur += 1
                if (start <= cur) {
                    remain = end - cur
                    msg = '剩余时间：'
                    if (__t.hour > 12) {
                        __t.content = msg + '> 一天'
                        window.clearInterval(inter)
                        return;
                    }
                    __t.disable = false
                } else {
                    remain = start - cur
                    __t.disable = true
                }
                __t.hour = parseInt(remain / 3600)
                __t.minute = parseInt(remain % 3600 / 60)
                __t.seconds = remain % 3600 % 60
                __t.content = msg + __t.hour + '时' + __t.minute + '分' + __t.seconds + '秒'

            }, 1000)
            // let inter2 = 
            // setInterval(function() {
            //     axios.get(__t.server + '/goods/num/' + __t.goods.goodId + '?' + new Date().getTime()).then(function(
            //         response) {
            //             console.log(response)
            //         // __t.goods.num = response.data
            //     })
            // }, 1000)
        }
    }
</script>

<style>
    .ttt > div ,
    .ttt > button {
        margin: 10px;
    }
</style>
