<template>
    <!-- <el-card class="box-card"> -->
    <div class="logpanel">
        <div class="top">
            <span><strong>排序：</strong></span>
            <el-radio-group v-model="reverse">
                <el-radio :label="true">倒序</el-radio>
                <el-radio :label="false">正序</el-radio>
            </el-radio-group>
        </div>
        <div class="log">
            <el-timeline :reverse="reverse">
                <el-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.timestamp">
                    <el-card class="timeline">
                        <h3>{{activity.content}}</h3>
                        <p>{{activity.timestamp}}</p>
                    </el-card>
                </el-timeline-item>
            </el-timeline>
        </div>
    </div>
    <!-- </el-card> -->
</template>

<script>
    const log = {
        ac: [{
            content: '服务器启动',
            timestamp: new Date().toLocaleDateString() + ' ' + new Date().toLocaleTimeString()
        }],
        data() {
            return {
                reverse: true,
                activities: log.ac
            }
        },
        put: function(msg) {
            log.methods.put(msg)
        },
        methods: {
            put: function(msg) {
                log.ac.push({
                    content: msg,
                    timestamp: new Date().toLocaleDateString() + ' ' + new Date().toLocaleTimeString()
                })
            }
        }
    }
    export default log;
</script>

<style>
    .logpanel {
        position: absolute;
        top: 60px;
        left: 20px;
        width: calc(100vw - 50px);
        height: calc(100vh - 60px);
        overflow-x: hidden;
        overflow-y: auto;
    }

    .logpanel>.top {
        position: relative;
        left: 40px;
        margin-top: 10px;
        
        margin-bottom: 20px;
    }
    
    .logpanel>.top > span {
        padding-right: 10px;
    }

    .timeline {
        display: flex;
        position: relative;
        width: 98%;
    }
</style>
