<template>
    <el-header class="pp">
        <div class="header-left"> </div>
        <div class="header-center">buyGo</div>
        <div class="header-right">
            <span v-if="!this.$store.state.user.isExist">请登录</span>
            <el-popover v-else placement="bottom" width="200" trigger="click">
                <el-upload class="upload-demo" ref="upload" :action="this.$store.state.s + '/user/uploadAvatar'" :limit="1"
                    :file-list="fileList" :auto-upload="false">
                    <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                    <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
                <el-avatar slot="reference" :src="avatar"></el-avatar>
            </el-popover>
        </div>
    </el-header>
</template>

<script>
    import axios from '../httpConfig/http.js'
    export default {
        data() {
            return {
                fileList: [],
                avatar: this.$store.state.s + '/user/downloadAvatar'
            }
        },
        methods: {
            submitUpload() {
                if (this.$refs.upload.uploadFiles.length <= 0) {
                        this.$notify({
                        title: '警告',
                        message: '请选择要上传的头像',
                        type: 'warning'
                    });
                    return ;
                }
                let __t = this;
                let forms = new FormData()
                let configs = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                forms.append('file', this.$refs.upload.uploadFiles[0].raw)
                console.log(this.$refs.upload.uploadFiles[0])
                axios.post(this.$store.state.s + '/user/uploadAvatar', forms, configs).then(res => {
                    __t.avatar = __t.avatar + '?' + new Date().getTime()
                    
                    __t.$refs.upload.uploadFiles.length = 0;
                    console.log(res)
                })
                // this.$refs.upload.submit();
            },
            setShow() {
                this.$store.state.aside_show = !this.$store.state.aside_show;
            }
        }
    }
</script>

