import VueRouter from 'vue-router'

import Order from '../components/self/Order.vue'
import Home from '../components/body/Home.vue'
import LOR from '../components/LOR.vue'
import Goods from '../components/self/Goods.vue'
import User from '../components/self/User.vue'
import Auth from '../components/self/Auth.vue'
import Role from '../components/self/Role.vue'
import Log from '../components/self/Log.vue'
import Statistics from '../components/self/Statistics.vue'

export default new VueRouter({
    routes: [{
            path: '/statistics',
            component: Statistics,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/',
            component: LOR,
            meta: {
                requireAuth: false
            }
        },
        {
            path: '/lor',
            component: LOR,
            meta: {
                requireAuth: false
            }
        },
        {
            path: '/user',
            component: User,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/home',
            component: Home,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/order',
            component: Order,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/goods',
            component: Goods,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/auth',
            component: Auth,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/role',
            component: Role,
            meta: {
                requireAuth: true
            }
        },
        {
            path: '/log',
            component: Log,
            meta: {
                requireAuth: true
            }
        }
    ]
})
