<template>
    <DataView v-bind:data="data"></DataView>
</template>

<script>
    import DataView from '../body/DataView.vue'
    export default {
        data() {
            return {
                data: {
                    type: 'order',
                    map: { // 映射
                        orderId: {name:'订单ID'},
                        goodId: {name:'商品ID'},
                        userId: {name:'用户ID'},
                        num: {name:'数量'},
                        createDate: {name:'下单时间'},
                        updateDate: {name:'更新时间'}
                    },
                    tableCols1: [{ // 表格列
                            prop: 'orderId',
