<template>
    <div class="content-body" v-if="page !== 'none'">
        <el-row type="flex" justify="center" style="width: 100%;height: 450px;margin-bottom: 10px;" v-for="i in parseInt(page.elist.length/4)+1" :key="i">
            <el-col style="height: 100%;max-width: 300px;" :span="6" v-for="j in 4" :key="j">
                <GoodsCard v-if="(i-1)*4+j-1 < page.elist.length" :server="server" :goods="page.elist[(i-1)*4+j-1]"></GoodsCard>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    import store from '../../store/index.js'
    import axios from '../../httpConfig/http.js'
    import GoodsCard from '../self/GoodsCard.vue'
    // import store 
    export default {
        data() {
            return {
                server: store.state.s,
                curPage: 1,
                pageSize: 10,
                search: 'goodId',
                search_content: '',
                page: 'none'
            }
        },
        methods: {
            update() {
                let __t = this;
                axios.post(__t.server + '/' + 'goodlist', {
                    curPage: __t.curPage,
                    pageSize: __t.pageSize,
                    search: __t.search,
                    search_content: __t.search_content
                }).then(function(response) {
                    if (response.data.code !== 200) {
                        // Log.put('查看' + __t.data.type + '表的第'+__t.curPage+'页表格失败')
                        __t.$notify.error({
                            title: '错误',
                            message: '错误码:' + response.data.msg
                        });
                        return;
                    }
                    // Log.put('查看了' + __t.data.type + '表的第'+ __t.curPage+'页表格')
                    __t.page = response.data.data.list;
                    console.log(__t.page.elist.length)
                })
            }
        },
        mounted() {
            this.update()
        },
        components:{
            GoodsCard
        }

    }
</script>

<style>
    .content-body {
        /* display: flex; */
        position: absolute;
        width: calc(100vw - 40px);
        left: 30px;
        height: calc(100vh - 60px);
        top: 50px;
        overflow-x: hidden;
    }
</style>
