<template>
    <div class="table-back">
        <el-dialog title="编辑" :visible.sync="data.dialogVisible" width="30%" :before-close="handleClose" :center="true">
            <div v-for="item in data.temp" :key="data.map[item.prop].name">
                <div v-if="data.map[item.prop].type === 'date'" class="input-label el-input el-input-group el-input-group--prepend">
                    <div class="el-input-group__prepend">{{data.map[item.prop].name}}</div>
                    <el-date-picker v-model="item.model" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="选择日期时间">
                    </el-date-picker>
                </div>

                <div v-else-if="data.map[item.prop].type == 'img'"></div>
                <el-input class="input-label" v-else v-model="item.model" :disabled="item.disabled">
                    <template slot="prepend">{{data.map[item.prop].name}}</template>
                </el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="data.dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="save">保 存</el-button>
            </span>
        </el-dialog>
        <el-row type="flex" width="100" class="op-panel" justify="center" style="">
            <el-col>
                <el-button type="primary" @click="openDialog(-1)">添加</el-button>
            </el-col>
            <el-col>
                <el-button type="primary" @click="exportExcel">导出</el-button>
            </el-col>
        </el-row>
        <el-input placeholder="请输入内容" v-model="search_content" class="home-search input-with-select">
            <el-select v-model="search" slot="prepend" placeholder="请选择" style="width: 100px;">
                <el-option v-for="item in data.option" :key="item.prop" :label="data.map[item.prop].name" :value="item.prop"></el-option>
            </el-select>
            <el-button slot="append" icon="el-icon-search" @click="update"></el-button>
        </el-input>
        <el-table id="exportTab" class="home-table" :data="data.page.elist" height="calc(100vh - 170px)" size="small">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item v-if="data.type === 'good'" :label="'商品展示'">
                            <ImgUpload :u="server + '/goods/uploadImg/' + props.row.name" :d="server + '/goods/downloadImg/' + props.row.name" :wid="'150px'"></ImgUpload>
                        </el-form-item>
                        <el-form-item v-for="(item,key) in props.row" :key="key" :label="data.map[key].name">
                            <div v-if="data.map[key].type == 'img'"> 
                                <img style="width: 50px;height: 50px;" :src="server+'/user/downloadAvatar/'+props.row.username"
                                    alt="" /></div>
                            <div v-else>{{item}}</div>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column sortable v-for="item in data.tableCols1" :key="item.prop" :prop="item.prop" :label="data.map[item.prop].name"
                :width="item.width">
            </el-table-column>
            <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                    <el-button type="text" @click="openDialog(scope.row[data.type+'Id'])">编辑</el-button>
                    <el-button type="text" style="color: red;" @click="del(scope.row[data.type+'Id'])">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="curPage"
            :page-sizes="[10, 20, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="data.page.total"
            class="home-pagesegment">
        </el-pagination>
    </div>
</template>

<script>
    import axios from 'axios'
    import store from '../../store'
    import FileSaver from 'file-saver'
    import XLSX from 'xlsx'
    import Log from '../self/Log.vue'
    import ImgUpload from '../self/ImgUpload.vue'

    export default {
        data() {
            return {
                server: store.state.s,
                curPage: 1,
                pageSize: 10,
                search: this.data.type + 'Id',
                search_content: ''
            }
        },
        props: {
            data: null
        },
        methods: {
            exportExcel() {
                let xlsxParam = {
                    raw: true
                }; // 导出的内容只做解析，不进行格式转换(添加此行代码表格中的百分比就不会再导出的时候被转换成小数点)
                let wb = XLSX.utils.table_to_book(
                    document.querySelector("#exportTab"),
                    xlsxParam
                );
                let wbout = XLSX.write(wb, {
                    bookType: "xlsx",
                    bookSST: true,
                    type: "array"
                });
                Log.put('导出了' + this.data.type + '表的第'+this.curPage+'页表格')
                // 获取当前的时间戳，用来当文件名
                let time = new Date().getTime();
                try {
                    FileSaver.saveAs(
                        new Blob([wbout], {
                            type: "application/octet-stream"
                        }),
                        `${time}.xlsx`
                    ); //${time}是自己导出文件时的命名，随意
                } catch (e) {
                    if (typeof console !== "undefined") console.log(e, wbout);
                }
                return wbout;
            },
            update() {
                const __t = this;
                
                axios.post(store.state.s + '/' + __t.data.type + 'list', {
                    curPage: __t.curPage,
                    pageSize: __t.pageSize,
                    search: __t.search,
                    search_content: __t.search_content
                }).then(function(response) {
                    if (response.data.code !== 200) {
                        Log.put('查看' + __t.data.type + '表的第'+__t.curPage+'页表格失败')
                        __t.$notify.error({
                            title: '错误',
                            message: '错误码:' + response.data.msg
                        });
                        return;
                    }
                    Log.put('查看了' + __t.data.type + '表的第'+ __t.curPage+'页表格')
                    __t.data.page = response.data.data.list;
                })
            },
            del(id) {
                const __t = this.data;
                const __this = this;
                let params = {}
                params[__t.type + 'Id'] = id;
                axios.get(store.state.s + '/' + __t.type + '/del', {
                    params: params
                }).then(function(response) {
                    if (response.data.code !== 200) {
                        __this.$notify.error({
                            title: '错误码:' + response.data.code,
                            message: response.data.msg,
                            duration: 10000
                        });
                        return;
                    }
                    Log.put('删除了' + __t.type + '表的ID:'+id+'数据')
                    __this.update();
                })
            },
            save() {
                const __t = this.data;
                const __this = this;
                let json = {}
                for (let i = 0; i < __t.temp.length; i++) {
                    if (__t.temp[i].model !== "自动生成") {
                        json[__t.temp[i].sec] = __t.temp[i].model;
                    }
                }
                axios.post(store.state.s + '/' + __t.type + '/sou', json).then(function(response) {
                    if (response.data.code !== 200) {
                        __this.$notify.error({
                            title: '错误码:' + response.data.code,
                            message: response.data.msg,
                            duration: 10000
                        });
                        return;
                    }
                    Log.put('添加了' + __t.type + '表数据')
                    __this.update();
                })
                __t.dialogVisible = false;
            },
            openDialog(id) {
                let __t = this.data;
                let data = this.data.page.elist;
                // 打开对话框设置默认值
                if (id == -1) {
                    let j = 0;
                    for (let t in data[0]) {
                        if (__t.temp[j].disabled) {
                            __t.temp[j].model = "自动生成";
                        } else __t.temp[j].model = "";
                        __t.temp[j++]['sec'] = t;
                    }
                } else {
                    for (let i = 0; i < data.length; i++) {
                        if (data[i][__t.type + 'Id'] == id) {
                            let j = 0;
                            for (let t in data[i]) {
                                __t.temp[j].model = data[i][t];
                                __t.temp[j++]['sec'] = t;
                            }
                            break;
                        }
                    }
                }
                __t.dialogVisible = true;
            },
            handleClose(done) {
                this.$confirm('确认关闭(不保存)？')
                    .then(() => {
                        done();
                    }).catch(function(){
                        
                    })
            },
            handleSizeChange(val) {
                this.pageSize = val;
            },
            handleCurrentChange(val) {
                this.curPage = val
                this.update()
            }
        },
        mounted() {
            this.update()
        },
        components:{
            ImgUpload
        }
    }
</script>

<style>
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .op-panel {
        position: absolute;
        top: 45px;
        left: 25px;

    }

    .op-panel>div>button {
        margin: 5px;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }

    .home-search {
        position: absolute;
        left: 195px;
        width: calc(100vw - 205px);
        top: 50px;
    }

    .el-date-editor.el-input,
    .el-date-editor.el-input__inner {
        width: 100% !important;
    }

    .el-input {
        /* margin: 5px; */
    }

    .input-label {
        margin-top: 5px;
    }

    .home-table {
        position: absolute;
        font-size: 15px;
        top: 100px;
        left: 30px;
        width: calc(100vw - 40px);
        box-shadow: 0px 0px 5px lightgray;
    }

    .home-pagesegment {
        position: absolute;
        display: flex;
        justify-content: center;
        top: calc(100vh - 60px);
        left: 30px;
        width: calc(100vw - 60px);
        padding: 10px;
        background-color: white;
        box-shadow: 0px 0px 5px lightgray;
    }
</style>
