<template>
    <el-popover placement="bottom" width="200" trigger="click">
        <el-upload class="upload-demo" ref="upload" :action="u" :limit="1" :file-list="fileList" :auto-upload="false">
            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
            <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>
        <el-image slot="reference" :style="'width:' + wid + ';height:' + wid" :src="dd" :fit="'fit'"></el-image>
    </el-popover>
</template>

<script>
    import axios from '../../httpConfig/http.js'
    export default {
        data() {
            return {
                fileList: [],
                dd: this.d
            }
        },
        props: {
            u: null,
            d: null,
            wid: null
        },
        methods: {
            submitUpload() {
                if (this.$refs.upload.uploadFiles.length <= 0) {
                    this.$notify({
                        title: '警告', 
                        message: '请选择要上传的头像',
                        type: 'warning'
                    });
                    return;
                }
                let __t = this;
                let forms = new FormData()
                let configs = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                forms.append('file', this.$refs.upload.uploadFiles[0].raw)
                axios.post(__t.u, forms, configs).then(() => {
                    __t.dd = __t.dd + '?' + new Date()
                    __t.$refs.upload.uploadFiles.length = 0;
                })
            }
        }
    }
</script>

