<template>
    <div class="content-body">
        <div style="height: 100%;width: 100%;" id="charts1">

        </div>
        <div style="height: 100%;width: 100%;" id="charts2">

        </div>
    </div>
</template>

<script>
    import echarts from 'echarts'
    import axios from '../../httpConfig/http.js'
    export default {
        data() {
            return {
                option1: {
                    title: {
                        text: '近7天的下单量统计'
                    },
                    xAxis: {
                        type: 'category',
                        data: []
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [],
                        type: 'line',
                        smooth: true
                    }]
                },
                option2: {
                    title: {
                        text: '用户下单量统计'
                    },
                    legend: {
                        top: 'bottom'
                    },
                    toolbox: {
                        show: true,
                        feature: {
                            mark: {
                                show: true
                            },
                            dataView: {
                                show: true,
                                readOnly: false
                            },
                            restore: {
                                show: true
                            },
                            saveAsImage: {
                                show: true
                            }
                        }
                    },
                    series: [{
                        type: 'pie',
                        radius: [50, 250],
                        center: ['50%', '50%'],
                        roseType: 'area',
                        itemStyle: {
                            borderRadius: 8
                        },
                        data: []
                    }]
                }
            }
        },
        mounted() {
            this.initEcharts()
        },
        methods: {
            initEcharts() {
                let charts1 = this.option1;
                let myChart1 = echarts.init(document.getElementById('charts1'));

                let day = 7;
                charts1.series[0].data = new Array(day)
                charts1.xAxis.data = Array.from(new Array(day), (x, i) => day - i - 1 + '天前')
                charts1.xAxis.data[day - 1] = '今天';
                charts1.series[0].data.fill(0)


                let charts2 = this.option2;
                let myChart2 = echarts.init(document.getElementById('charts2'));
                let od = {}
                axios.get(this.$store.state.s + '/order/statistics?day=' + day, ).then((response) => {
                    let data = response.data.data.data
                    for (let i in data) {
                        charts1.series[0].data[day - 1 - parseInt((new Date() - new Date(data[i].updateDate)) /
                            1000 / 60 / 60 / 24)]++
                        let id = data[i].userId + ''
                        if (od[id] === undefined)
                            od[id] = {
                                value: 1,
                                name: 'ID:' + data[i].userId
                            }
                        else od[id].value++
                    }
                    for(let v in od) {
                        charts2.series[0].data.push(od[v])
                    }
                    myChart1.setOption(charts1)
                    myChart2.setOption(charts2)

                    window.addEventListener("resize", function() {
                        myChart1.resize();
                        myChart2.resize()
                    });
                })
                // 使用刚指定的配置项和数据显示图表。
            }

        }
    }
</script>

